@import "src/uikit/Theme/theme.module.scss";

.base {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: stretch;
  box-sizing: border-box;
}

.space2 {
  gap: 2px;
}
.space4 {
  gap: 4px
}

.paddingsForm {
  padding: 16px;
}

.paddingsMain {
  padding: 0;
}

.bgDefault {
  background-color: $surfaceDefault;
}
.bgLight{
  background-color: $surfaceLight
}

.bgDark {
  background-color: $backgroundDark;
}

.bgOnboarding {
  background-image: url('./onboarding_screen.svg');
}

