.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;
}

.space0 > :not(:first-child):not(:empty) {
  margin-left: 0px;
}

.space4 > :not(:first-child):not(:empty) {
  margin-left: 4px;
}

.space8 > :not(:first-child):not(:empty) {
  margin-left: 8px;
}

.space12 > :not(:first-child):not(:empty) {
  margin-left: 8px;
}

.space16 > :not(:first-child):not(:empty) {
  margin-left: 16px;
}

.space24 > :not(:first-child):not(:empty) {
  margin-left: 24px;
}


